import {signInWithEmailAndPassword } from 'firebase/auth'
import React from 'react'
import { useState } from 'react'
import { auth } from '../../firebase'


const SingIn = () => {
    const [name,setName] = useState("")
    const [password,setPassword] = useState("")
    const [email,setEmail] = useState("")
    const [error,setError] = useState("")

    const nameHanlder = (event)=>{
        setName(event.target.value)
    }
    const passwordHanlder = (event) =>{
        setPassword(event.target.value)
    }

    const emailHanlder = (event)=>{
        setEmail(event.target.value)
    }

    function logIn(e){
        e.preventDefault()
        signInWithEmailAndPassword(auth,email,password) //  auth(  ,  )(signInWithEmailAndPassword    )
        .then((user) =>{
            console.log(user)
            setEmail("")
            setPassword("")
            setError("")
            setName("")
        }).catch((err)=>{
            console.log(err)
            setError("      ")
        })

    }
  return (
    <div>
        <form>
            <h2>  </h2>
            <label htmlFor='name'></label>
            <input type='text' id='name' onChange={nameHanlder} value={name}/>
            <label htmlFor='email'></label>
            <input type='email' id='email' onChange={emailHanlder} value={email}/>
            <label htmlFor='password'></label>
            <input type='password'  onChange={passwordHanlder} value={password}/>
            <button onClick={logIn} style={{color:"White"}}></button>
            {error? <p style={{color:"red"}}>{error}</p> : ""}
        </form>
    </div>
  )
}

export default SingIn