import { onAuthStateChanged, signOut } from 'firebase/auth'
import React, { useEffect, useState } from 'react'
import { auth } from '../../firebase'
import "./auntification.css"
const AuthDetails = () => {
    const [authUser,setAuthUser] = useState(null)
    useEffect(()=>{
        const listen = onAuthStateChanged(auth,(user)=>{ //     user 
            if(user){
                setAuthUser(user)
            }else{
                setAuthUser(null)
            } //     (useState)  null
        })
        return() =>{
            listen()
        }
    },[])

    //     
    const userSignOut = ()=>{
        signOut(auth)
        .then(() => console.log("success"))
        .catch((err) =>{
            console.log(err)
        })
    }
  return (
    <div className='auntification'>
        {/*            email    (  <p>Signed OUT)*/}
        {authUser?(
            <div><p>{`   ${authUser.email}`}</p><button onClick={userSignOut}></button></div>
        ): <p>Signed out</p>}
    </div>
  )
}

export default AuthDetails